.banner-container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
}

.projects-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
    max-width: 1000px;
    width: 100%;
}

.project-card {
    background-color: rgba(255, 255, 255, 0.05);
    border: 2px solid white;
    border-radius: 12px;
    padding: 20px;
    color: white;
    text-align: center;
    transition: transform 0.3s;
}

.project-card:hover {
    transform: scale(1.05);
}

.project-card h2 {
    margin-bottom: 10px;
    font-size: 24px;
}

.project-card p {
    margin-bottom: 10px;
    font-size: 18px;
}

.project-card .tech {
    font-size: 16px;
    color: #61dafb;
    margin-bottom: 15px;
}
